export default class Post {
  constructor(node, summary = false) {
    // node 
    if (!node) {
      this.id = '';
      this.html = '';
      this.excerpt = '';
      this.slug = '';
      this.title = '';
      this.date = '';
      this.categories = [];
      this.author = '';
      this.emoji = '';
      this.imageUrl = '';
      return;
    }

    const { id = '', html = '', excerpt = '', frontmatter = {}, fields = {} } = node;
    const {
      title = '',
      date = '',
      categories = '',
      author = '',
      emoji = '',
      imageUrl = ''
    } = frontmatter;
    const { slug = '' } = fields;

    this.id = id;
    this.excerpt = excerpt;
    this.slug = slug;
    this.title = title;
    this.date = date;
    this.categories = categories ? categories.split(' ').filter(Boolean) : [];
    this.author = author;
    this.emoji = emoji;
    this.imageUrl = imageUrl;

    if (!summary) {
      this.html = html;
    }
  }
}