import { useStaticQuery, graphql } from 'gatsby';
import React from 'react';
import { Helmet } from 'react-helmet';

function Seo({ description, title }) {
    const { site } = useStaticQuery(
        graphql`
      query {
        site {
          siteMetadata {
            title
            description
            author {
              name
            }
            ogImage
          }
        }
      }
    `,
    );

    // site    
    if (!site || !site.siteMetadata) {
        return (
            <Helmet
                htmlAttributes={{ lang: 'en' }}
                title={title || 'Loading...'}
            />
        );
    }

    const metaDescription = description || site.siteMetadata.description || '';
    const siteTitle = site.siteMetadata.title || 'Blog';
    const authorName = site.siteMetadata.author?.name || 'Author';
    const ogImage = site.siteMetadata.ogImage || '';

    return (
        <Helmet
            htmlAttributes={{ lang: 'en' }}
            title={title}
            defaultTitle={siteTitle}
            meta={[
                {
                    property: `og:title`,
                    content: title || siteTitle,
                },
                {
                    property: `og:site_title`,
                    content: title || siteTitle,
                },
                {
                    name: `description`,
                    content: metaDescription,
                },
                {
                    property: `og:description`,
                    content: metaDescription,
                },
                {
                    property: 'og:author',
                    content: authorName,
                },
                {
                    property: 'og:image',
                    content: ogImage,
                },
                {
                    property: `og:type`,
                    content: `website`,
                },
                //google seo
                {
                    name:`google-site-verification`,
                    content:`NIs0QhmetlWroylVB41Dz3fVQcuHAzP8e2DvVMjFPus`,
                },
                //naver seo
                {
                    name:`naver-site-verification`,
                    content:`c695577700808f46f7ac9db8f39c8a2a3f9dd14d`,
                },
            ]}
        />
    );
}

export default Seo;